const HomePage = () => {
  const [currentLanguage, setCurrentLanguage] = useState('en');
  const [isLoading, setIsLoading] = useState(true);
  const [services, setServices] = useState([]);
  const [reviews, setReviews] = useState([]);
  const [preFilledEstimate, setPreFilledEstimate] = useState(null);
  const [currentArea, setCurrentArea] = useState(null);
  
  const location = useLocation();

  // Detect current area from URL
  useEffect(() => {
    const path = location.pathname;
    const areaMatch = path.match(/^\/areas\/([^\/]+)/);
    
    if (areaMatch) {
      const areaKey = areaMatch[1];
      const areaConfig = AREA_CONFIGS[areaKey];
      if (areaConfig) {
        setCurrentArea(areaConfig);
        // Update page title for area-specific content
        document.title = areaConfig.title + ' | Santos Cleaning Solutions';
      }
    } else {
      setCurrentArea(null);
      // Reset to default title
      document.title = 'House Cleaning Services Marietta GA | Buckhead Atlanta | Santos Cleaning Solutions';
    }
  }, [location.pathname]);

  useEffect(() => {
    // Initialize page
    const initializePage = async () => {
      try {
        // Check API health
        await apiService.healthCheck();
        
        // Load services and reviews in parallel
        const [servicesData, reviewsData] = await Promise.all([
          // TEMPORÁRIO: Forçar uso dos constants atualizados até o backend ser atualizado
          Promise.resolve({ services: [] }),
          apiService.getReviews().catch((error) => {
            console.error('Failed to fetch reviews:', error);
            return { reviews: [] };
          })
        ]);
        
        // Forçar uso dos DEFAULT_SERVICES que têm preços corretos
        setServices([]);
        setReviews(reviewsData.reviews || []);
        
      } catch (error) {
        console.warn('Some data failed to load, using fallback data');
      } finally {
        setIsLoading(false);
      }
    };

    initializePage();

    // Language detection from localStorage or browser
    const savedLanguage = localStorage.getItem('language');
    const browserLanguage = navigator.language.split('-')[0];
    const supportedLanguages = ['en', 'es', 'pt'];
    
    if (savedLanguage && supportedLanguages.includes(savedLanguage)) {
      setCurrentLanguage(savedLanguage);
    } else if (supportedLanguages.includes(browserLanguage)) {
      setCurrentLanguage(browserLanguage);
    }
  }, []);

  const handleLanguageChange = (newLanguage) => {
    setCurrentLanguage(newLanguage);
    localStorage.setItem('language', newLanguage);
  };

  const handleEstimateReady = (estimateData) => {
    setPreFilledEstimate(estimateData);
    
    // Scroll to contact section
    const contactSection = document.getElementById('contact');
    if (contactSection) {
      contactSection.scrollIntoView({ behavior: 'smooth', block: 'start' });
      
             // Enhanced analytics tracking with estimate data
       if (typeof window !== 'undefined' && window.gtag) {
         window.gtag('event', 'estimate_completed', {
           'event_category': 'Calculator',
           'event_label': estimateData.serviceType,
           'value': estimateData.total,
           'custom_parameters': {
             'service_type': estimateData.serviceType,
             'estimated_total': estimateData.total,
             'square_feet': estimateData.sqft,
             'current_area': currentArea?.name || 'General'
           }
         });
       }
    }
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <div className="loading-spinner mx-auto mb-4"></div>
          <p className="text-gray-600">
            Loading Santos Cleaning Solutions
            {currentArea && ` - ${currentArea.name}`}...
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* SEO Head */}
      <HomePageSEO currentLanguage={currentLanguage} />
      
      <Header 
        currentLanguage={currentLanguage}
        onLanguageChange={handleLanguageChange}
        currentArea={currentArea}
      />
      
                                  <main id="main-content" role="main">
                {/* Main H1 for SEO */}
                <h1 className="sr-only">
                  {currentArea ? 
                    `Professional House Cleaning Services in ${currentArea.name}, GA - Santos Cleaning Solutions` : 
                    'Professional House Cleaning Services in Marietta & Buckhead, GA - Santos Cleaning Solutions'
                  }
                </h1>
                <HeroSection 
                  currentLanguage={currentLanguage} 
                  currentArea={currentArea}
                />
        <AboutSection 
          currentLanguage={currentLanguage}
          currentArea={currentArea}
        />
        <ServicesSection 
          currentLanguage={currentLanguage} 
          services={services}
          currentArea={currentArea}
        />
        <PriceCalculator 
          currentLanguage={currentLanguage} 
          onEstimateReady={handleEstimateReady}
          currentArea={currentArea}
        />
        <BeforeAfterSection 
          currentLanguage={currentLanguage}
          currentArea={currentArea}
        />
        <ReviewsSection 
          currentLanguage={currentLanguage} 
          reviews={reviews}
          currentArea={currentArea}
        />
        <ContactSection 
          currentLanguage={currentLanguage} 
          preFilledEstimate={preFilledEstimate}
          currentArea={currentArea}
        />
      </main>
      
      <Footer 
        currentLanguage={currentLanguage}
        currentArea={currentArea}
      />
      <StickyCTA 
        currentLanguage={currentLanguage}
        currentArea={currentArea}
      />
    </div>
  );
};

export default HomePage;