import React from 'react';
import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer';
import { useTranslations } from '../utils/translations';
import { COMPANY_INFO } from '../utils/constants';

const HeroSection = ({ currentLanguage, currentArea }) => {
  const { t } = useTranslations(currentLanguage);
  const [ref, inView] = useInView({
    threshold: 0.1,
    triggerOnce: true
  });

  const scrollToContact = () => {
    const element = document.getElementById('contact');
    if (element) {
      element.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  };

  // Get area-specific content or fallback to translations
  const getHeroTitle = () => {
    if (currentArea) {
      return currentArea.heroTitle;
    }
    return t('hero-title');
  };

  const getHeroSubtitle = () => {
    if (currentArea) {
      return currentArea.heroSubtitle;
    }
    return t('hero-subtitle');
  };

  const getSpecialMessage = () => {
    if (currentArea) {
      const focusMessages = {
        'luxury': '🏆 Luxury Focus: Specialized in high-end homes and executive communities',
        'family': '🏠 Family Focus: Local business serving families in the community',
        'historic': '🏛️ Historic Expertise: Specializing in historic homes and family-friendly neighborhoods',
        'executive': '💼 Executive Focus: Professional service for discerning homeowners and busy executives',
        'comprehensive': '🌆 Metro Coverage: Serving Greater Atlanta with reliable, professional service'
      };
      return focusMessages[currentArea.focus] || '✨ Professional cleaning services for your community';
    }
    return null;
  };

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        duration: 0.8,
        staggerChildren: 0.2
      }
    }
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.7, ease: [0.4, 0, 0.2, 1] }
    }
  };

  return (
    <section 
      id="home" 
      ref={ref}
      className="hero relative overflow-hidden min-h-screen flex items-center justify-center"
      style={{
        background: `linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.4)), url('https://images.unsplash.com/photo-1586023492125-27b2c045efd7?w=1920&h=1080&fit=crop&crop=center')`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundAttachment: 'fixed'
      }}
    >
      <div className="container mx-auto px-4 py-20 text-center relative z-10">
        <motion.div
          variants={containerVariants}
          initial="hidden"
          animate={inView ? "visible" : "hidden"}
          className="max-w-4xl mx-auto"
        >
          {/* Area-specific badge */}
          {currentArea && (
            <motion.div
              variants={itemVariants}
              className="inline-block mb-6"
            >
              <span className="bg-blue-600 text-white px-4 py-2 rounded-full text-sm font-semibold">
                Serving {currentArea.name}, GA
              </span>
            </motion.div>
          )}

          <motion.div 
            variants={itemVariants}
            className="text-4xl md:text-6xl font-bold text-white mb-6 leading-tight"
          >
            {getHeroTitle()}
          </motion.div>
          
          <motion.p 
            variants={itemVariants}
            className="text-xl md:text-2xl text-gray-200 mb-8 leading-relaxed"
          >
            {getHeroSubtitle()}
          </motion.p>

          {/* Area-specific special message */}
          {getSpecialMessage() && (
            <motion.div
              variants={itemVariants}
              className="bg-blue-800 bg-opacity-90 rounded-lg p-4 mb-8 max-w-2xl mx-auto"
            >
              <p className="text-white text-lg">
                {getSpecialMessage()}
              </p>
            </motion.div>
          )}

          {/* Area-specific specialties */}
          {currentArea && currentArea.specialties && (
            <motion.div
              variants={itemVariants}
              className="mb-8"
            >
              <div className="flex flex-wrap justify-center gap-3">
                {currentArea.specialties.map((specialty, index) => (
                  <span
                    key={index}
                    className="bg-white bg-opacity-20 text-white px-3 py-1 rounded-full text-sm font-medium"
                  >
                    {specialty}
                  </span>
                ))}
              </div>
            </motion.div>
          )}
          
          <motion.div 
            variants={itemVariants}
            className="flex flex-col sm:flex-row gap-4 justify-center items-center"
          >
            <button
              onClick={scrollToContact}
              className="bg-blue-600 hover:bg-blue-700 text-white font-bold py-4 px-8 rounded-lg text-lg transition-all duration-300 transform hover:scale-105 hover:shadow-xl"
            >
              {t('hero-cta')}
            </button>
            
            <a 
              href={`tel:${COMPANY_INFO.phone.replace(/\D/g, '')}`}
              className="border-2 border-white text-white hover:bg-white hover:text-blue-900 font-bold py-4 px-8 rounded-lg text-lg transition-all duration-300 transform hover:scale-105"
            >
              {t('hero-phone')}
            </a>
          </motion.div>

          {/* Area-specific neighborhoods mention */}
          {currentArea && currentArea.neighborhoods && (
            <motion.div
              variants={itemVariants}
              className="mt-8"
            >
              <p className="text-gray-300 text-sm">
                Serving: {currentArea.neighborhoods.slice(0, 3).join(' • ')}
                {currentArea.neighborhoods.length > 3 && ' • and more'}
              </p>
            </motion.div>
          )}
        </motion.div>
      </div>
      
      {/* Scroll indicator */}
      <motion.div 
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 1, duration: 1 }}
        className="absolute bottom-8 left-1/2 transform -translate-x-1/2"
      >
        <div className="w-6 h-10 border-2 border-white rounded-full flex justify-center">
          <div className="w-1 h-3 bg-white rounded-full mt-2 animate-bounce"></div>
        </div>
      </motion.div>
    </section>
  );
};

export default HeroSection;